.half-x-line {
  height: 1px;
  background: #9b9b9b;
  transform-origin: 0 0;
  transform: scale(1, 0.2);
  -webkit-transform-origin: 0 0;
  -webkit-transform: scale(1, 0.2);
}
.half-y-line {
  width: 1px;
  height: 100%;
  background: #333;
  transform-origin: 0 0;
  transform: scale(0.2, 1);
  -webkit-transform-origin: 0 0;
  -webkit-transform: scale(0.2, 1);
}
.Iinput {
  display: block;
  background: transparent;
  box-shadow: none;
  outline: none;
  border: 1px solid #cfdadd;
  color: #9e9e9e;
  font-size: 14px;
  padding: 6px 10px;
  border-radius: 2px;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  line-height: 120%;
}
.Iinput:focus {
  border-color: #23b7e5;
}
.Ilabel {
  color: #5d5e5e;
  font-size: 14px;
  font-weight: bold;
}
.layer {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
}
.no-touch {
  pointer-events: none;
}
.cursor {
  cursor: pointer;
}
.bac-image {
  background-size: auto 100%;
  background-position: center center;
  background-repeat: no-repeat;
}
.h-tag {
  background: #ecf0f1;
  display: inline-block;
  height: 22px;
  line-height: 23px;
  padding: 0 12px;
  border-radius: 22px;
  cursor: pointer;
  font-size: 12px;
  margin-right: 3px;
  margin-bottom: 5px;
}
.h-tag.active {
  background: #95a5a6;
  color: white;
}
.h-btn {
  border: 1px solid #ccd2d4;
  display: inline-block;
  height: 22px;
  line-height: 21px;
  padding: 0 5px;
  border-radius: 1px;
  cursor: pointer;
  margin-right: 3px;
  font-size: 12px;
}
.h-btn.md {
  height: 25px;
  line-height: 24px;
  padding: 0 10px;
}
.h-y-btn {
  height: 32px;
  width: 70px;
  border-radius: 26px;
  background: #81868c;
  color: white;
  font-size: 16px;
  cursor: pointer;
  line-height: 100%;
}



